import request from '@/utils/request.js';
import functionTool from '@/utils/functionTool.js';

export default {
	props:{
		shareMsg: Object,
		shareLikes: Array
	},
	
	computed:{
		indicatorDots(){
			return this.shareMsg.images.length > 1;
		},
		isLike(){
			return this.shareLikes.includes(this.shareMsg._id)
		}
	},
	
	data() {
		return {
			current: 0
		};
	},
	
	methods:{
		onChangeCurrent(e){
			this.current = e.detail.current;
		},
		onPreview(){
			const { images } = this.shareMsg;
			uni.previewImage({
				current: this.current,
				urls: images
			});
		},
		onLike:functionTool.throttle(async function(){
			const isLike = this.isLike;
			this.$emit('onLike',{ _id: this.shareMsg._id, isLike: !isLike });
			const { _id } = this.shareMsg;
			const res = await request({
				name:'likeShareMessage',
				data:{
					_id,
					isLike: !isLike
				},
				needLogin: 1
			});
			if(!res || res.status !== 0){
				this.$emit('onLike',{ _id: this.shareMsg._id, isLike:isLike });
			} else {
				this.$emit('updateShareLike');
		} }, 400),
	}
	
}
